<template>
  <el-card class="umis-setting__card-margin">
    <div slot="header" class="umis-setting__header">
      <span>|</span>
      <el-button plain size="mini" type="primary" @click="creatorDialog = true">
        
      </el-button>
    </div>
    <el-table :data="projectList">
      <el-table-column prop="projectName" label=" " width="140">
      </el-table-column>
      <el-table-column prop="projectIcon" label="" width="120">
      </el-table-column>
      <el-table-column prop="address" label=""> </el-table-column>
    </el-table>
    <el-dialog :visible.sync="creatorDialog">
      <el-form v-loading="apiLoading" :model="data" label-width="80px">
        <el-form-item label="">
          <el-input v-model="data.name" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="data.label" />
        </el-form-item>
        <el-form-item>
          <el-radio-group v-model="data.type">
            <el-radio label="project"></el-radio>
            <el-radio label="page"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="data.type && data.type === 'page'" label="">
          <el-select v-model="data.projectId" placeholder="">
            <template v-for="(item, index) in projectList" :key="index">
              <el-option :label="item.projectName" :value="item.projectId" />
            </template>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="data.icon" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="data.desc" type="textarea" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="creatorDialog = false"> </el-button>
        <el-button type="primary" @click="handleCreate"> </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
import {
  Form as ElForm,
  FormItem as ElFormItem,
  Input as ElInput,
  Radio as ElRadio,
  RadioGroup as ElRadioGroup,
  Select as ElSelect,
  Option as ElOption,
  Button as ElButton,
  Card as ElCard,
  Dialog as ElDialog,
  Table as ElTable,
  TableColumn as ElTableColumn,
} from 'element-ui';

export default {
  name: 'MisCreator',
  components: {
    ElForm,
    ElFormItem,
    ElInput,
    ElRadio,
    ElRadioGroup,
    ElSelect,
    ElOption,
    ElCard,
    ElDialog,
    ElButton,
    ElTable,
    ElTableColumn,
  },
  data() {
    return {
      data: {},
      projectList: [],
      apiLoading: false,
      creatorDialog: false,
    };
  },
  mounted() {
    this.fetchProjects();
  },
  methods: {
    fetchProjects() {
      this.$api
        .slientApi()
        .get('/api/project')
        .then(res => {
          const { rows } = res.data;
          this.projectList = rows;
        })
        .catch(error => {
          console.error(error);
        });
    },
    handleCreate() {
      this.apiLoading = true;
      this.$api
        .slientApi()
        .post(`/api/${this.data.type}`, this.data)
        .then(res => {
          console.log(res);
          this.creatorDialog = false;
        })
        .catch(error => {
          console.error(error);
        })
        .finally(() => {
          this.apiLoading = false;
        });
    },
  },
};
</script>
